import React, { useEffect, useState, PureComponent } from "react";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { connect } from "react-redux";

function Total(trade_datas) {
  const allDate = trade_datas.trade_datas.trade_datas.map((e) =>
    e.Stats.map((item) => ({
      Date: item.Date,
      Result: Math.floor(item.AvgPrice * item.TradeCount),
    }))
  );
  const combinedData1 = [].concat(...allDate);
  // //  
  function calculateResult(combinedData) {
    const result = {};
    combinedData.forEach((item) => {
      const { Date, Result } = item;
      if (!result[Date]) {
        result[Date] = 0;
      }
      result[Date] += Result;
    });
    return result;
  }
  const result1 = calculateResult(combinedData1);
  const data = Object.keys(result1).map((Date) => ({
    Date,
    "  ": result1[Date],
  }));
  const renderTooltipContent = (o) => {
    const { payload, label } = o;
    return (
      <div className="customized-tooltip-content">
        <p className="total">{`${label} `}</p>
        <ul className="list">
          {payload.map((entry, index) => (
            <li key={`item-${index}`} style={{ color: entry.color }}>
              {`${entry.name}: ${entry.value.toLocaleString()} G`}
            </li>
          ))}
        </ul>
      </div>
    );
  };
  const CustomYAxisTick = (props) => {
    const { x, y, payload } = props;
    const value = payload.value;
    // 1000   'k'   
    const formattedValue =
      value >= 1000000 ? `${(value / 1000000).toFixed(0)}` : value;
    return (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={0} dy={16} textAnchor="middle" fill="#666">
          {formattedValue}
        </text>
      </g>
    );
  };
  // const [chartWidth, setChartWidth] = useState(500); //   
  // const [chartHeight, setChartHeight] = useState(400);
  // useEffect(() => {
  //   const handleResize = () => {
  //     //           
  //     const newWidth = window.innerWidth >= 500 ? 500 : 375; // : 768px   500px,   300px 
  //     const newHeight = window.innerWidth >= 500 ? 400 : 300; // : 768px  
  //     setChartHeight(newHeight);
  //     setChartWidth(newWidth);
  //   };

  //   //          
  //   window.addEventListener("resize", handleResize);

  //   //      
  //   return () => {
  //     window.removeEventListener("resize", handleResize);
  //   };
  // }, []);

  return (
    <div>
      <h1>    </h1>
      <h6>=*</h6>
      <BarChart
        width={375}
        height={300}
        data={data}
        margin={{
          top: 5,
          right: 30,
          left: 20,
          bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="Date" reversed={true} />
        <YAxis tick={<CustomYAxisTick />} />
        <Tooltip content={renderTooltipContent} />
        <Legend />
        <Bar dataKey="  " fill="#8884d8" />
      </BarChart>
    </div>
  );
}

const mapStateToProps = (state) => ({
  // items: state.items,
  // lists: state.lists,
  trade_datas: state.trade_datas,
});
export default connect(mapStateToProps)(Total);
